/* Фон приветствия */

div#main-info {
    width: 100%;
    //background-image: url("../../assets/images/wall.jpg");
    //background-size: cover;
    //background-position-y: -280px; /* Задаёт положение фонового изображения внутри элемента по вертикали. */
    
    display: flex;
    justify-content: space-between;

    div.main-info-wrapper {
        box-sizing: border-box;
        padding-right: 10%;
        padding-left: 8%;
        width: 100%;
        height: 100%;
    
        display: flex;
        justify-content: space-between;
        /*align-items: baseline;*/

        div#slogan {
            font-family: $font;
            font-size: 22px;
            line-height: 1.5em;
            width: 400px;
            margin: 50px 0 0 0px;
            color: white;
        
            text-shadow: #808080 0 0 1px;

            h2 {
                font-size: 32px;
            }
        }

        #forma1 {
            box-sizing: border-box;
            padding: 0 10px 0 10px;
            height: 400px;
            width: 400px;
            border-radius: 15px;
            background: rgba(128, 128, 128, 0.4);
            margin-top: 50px; /* Выравниваем в форму в нашем div */
            margin-bottom: 20px;

            h3 {
                margin: 0px;
                padding-top: 10px;
                text-align: center;
                text-shadow: #000 0 0 3px; /* */
                color: white;
            }

            .field {
                box-sizing: border-box;
                width: 100%;
                height: 30px;
                margin: 10px 0 10px 0;
                border-radius: 10px;
                border-style: none;
                padding: 0 10px 0 10px;
                font-size: 14px;
                outline: none;
            }

            .textarea {
                box-sizing: border-box;
                width: 100%;
                height: 70px;
                margin-top: 10px;
                
                border-radius: 10px;
                border-style: none;
                padding: 10px;
                resize: none; /* Запрещаем изменять размер */
                font-size: 14px;
                outline: none;
            }

            label {
                margin-right: 10px;
            }

            button[type=submit] {
                width: 100px;
                height: 30px;
                border-radius: 10px;
                border-style: none;
                background-color: #1049A9;
                font-size: 16px;
                color: white;
                float: right;
                margin: 15px 0 0 0;
                outline: 0;
                cursor: pointer;

                &:hover {
                    animation-name: colorchange;
                    animation-duration: .7s;
                    background-color: #052C6E;
                }
            }

            p, #buttons1 {
                text-shadow: #000 0 0 3px; /* */
                color: white;
            }
        }
    }
}

/* Стилизую радиокнопки */
input[type=radio] {
    position: absolute;
    z-index: -1;
    opacity: 0;
    /* Определяет уровень прозрачности элемента веб-страницы. Значение 0 соответствует полной прозрачности элемента, 1, наоборот — его непрозрачности. */
    margin: 10px 0 0 7px;
}
  
input[type=radio] + label {
    position: relative;
    padding: 0 0 0 35px;
    cursor: pointer;
}
  
input[type=radio] + label:before {
    content: "";
    position: absolute;
    top: -3px;
    left: 0;
    width: 22px;
    height: 22px;
    border: 1px solid #052C6E;
    border-radius: 50%;
    background: #FFF;
}
  
input[type=radio] + label:after {
    content: "";
    position: absolute;
    top: 1px;
    left: 4px;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #1049A9;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: 0.2s;
}
  
input[type=radio]:checked + label:after {
    opacity: 1;
}
  
input[type=radio]:focus + label:before {
    box-shadow: 0 0 0 3px rgba(0, 82, 198, 0.7);
}

@media screen and (max-width: 500px) {
    div.main-info-wrapper {
        flex-direction: column;
        #forma1 {
            width: auto !important;
        }
        #slogan {
            width: auto !important;
        }
    }
}